import { useState, useEffect } from 'react';
import { useAuth } from '@/context/AuthContext';
import axiosInstance from '@/api/axios/axiosInstance';

interface Permission {
  id: number;
  name: string;
  display_name: string;
  description: string;
  category: string;
}

interface Role {
  id: number;
  name: string;
  display_name: string;
  description: string;
  is_system_role: boolean;
}

interface PageAccess {
  page_path: string;
  page_name: string;
  description: string;
}

interface UsePermissionsReturn {
  // Permission checking
  hasPermission: (permissionName: string) => boolean;
  hasAnyPermission: (permissionNames: string[]) => boolean;
  hasAllPermissions: (permissionNames: string[]) => boolean;
  
  // Role checking
  hasRole: (roleName: string) => boolean;
  hasAnyRole: (roleNames: string[]) => boolean;
  isSuperAdmin: boolean;
  
  // Page access checking
  canAccessPage: (pagePath: string) => boolean;
  
  // Data
  permissions: Permission[];
  role: Role | null;
  accessiblePages: PageAccess[];
  
  // Loading states
  loading: boolean;
  error: string | null;
  
  // Refresh function
  refresh: () => Promise<void>;
}

export const usePermissions = (): UsePermissionsReturn => {
  const { user, isLoggedIn } = useAuth();
  const [permissions, setPermissions] = useState<Permission[]>([]);
  const [role, setRole] = useState<Role | null>(null);
  const [accessiblePages, setAccessiblePages] = useState<PageAccess[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const fetchUserPermissions = async () => {
    if (!user?.role_id || !isLoggedIn) {
      setPermissions([]);
      setRole(null);
      setAccessiblePages([]);
      setLoading(false);
      return;
    }

    try {
      setLoading(true);
      setError(null);

      // Fetch all data in parallel for better performance
      const [roleResponse, permissionsResponse, pagesResponse] = await Promise.all([
        axiosInstance.get(`/api/roles/${user.role_id}`),
        axiosInstance.get(`/api/permissions/user/${user.id}/detailed`),
        axiosInstance.get('/api/page-permissions/accessible/me')
      ]);

      // Process responses
      if (roleResponse.data) {
        setRole(roleResponse.data);
      }

      if (permissionsResponse.data.success) {
        setPermissions(permissionsResponse.data.data || []);
      }

      if (pagesResponse.data.success) {
        setAccessiblePages(pagesResponse.data.data || []);
      }

    } catch (err: any) {
      console.error('Error fetching permissions:', err);
      setError(err.response?.data?.message || 'Failed to load permissions');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchUserPermissions();
  }, [user?.role_id, user?.id, isLoggedIn]);

  // Permission checking functions
  const hasPermission = (permissionName: string): boolean => {
    if (!user || !isLoggedIn) return false;
    
    // Super admin has all permissions
    if (isSuperAdmin) return true;
    
    return permissions.some(p => p.name === permissionName);
  };

  const hasAnyPermission = (permissionNames: string[]): boolean => {
    if (!user || !isLoggedIn) return false;
    
    // Super admin has all permissions
    if (isSuperAdmin) return true;
    
    return permissionNames.some(name => hasPermission(name));
  };

  const hasAllPermissions = (permissionNames: string[]): boolean => {
    if (!user || !isLoggedIn) return false;
    
    // Super admin has all permissions
    if (isSuperAdmin) return true;
    
    return permissionNames.every(name => hasPermission(name));
  };

  // Role checking functions
  const hasRole = (roleName: string): boolean => {
    if (!role) return false;
    return role.name === roleName;
  };

  const hasAnyRole = (roleNames: string[]): boolean => {
    if (!role) return false;
    return roleNames.includes(role.name);
  };

  // Super admin check
  const isSuperAdmin = role?.name === 'super_admin' || user?.role_id === 1;

  // Page access checking
  const canAccessPage = (pagePath: string): boolean => {
    if (!user || !isLoggedIn) return false;
    
    // Super admin can access all pages
    if (isSuperAdmin) return true;
    
    return accessiblePages.some(page => page.page_path === pagePath);
  };

  const refresh = async () => {
    await fetchUserPermissions();
  };

  return {
    hasPermission,
    hasAnyPermission,
    hasAllPermissions,
    hasRole,
    hasAnyRole,
    isSuperAdmin,
    canAccessPage,
    permissions,
    role,
    accessiblePages,
    loading,
    error,
    refresh
  };
};

export default usePermissions;
